/**********
* Main stylesheet
**********/

html {
  height: 100%;
}

* {
  box-sizing: border-box;
}

body {
  background: url(../img/main-bg.jpg) center center no-repeat;
  background-size: cover;
  height: 100%;
  font-family: 'Roboto', sans-serif;
}

#main-block {
  width: 100%;
  max-width: 800px;

  margin: 0 auto;
  position: relative;
  height: 100%;
}

.main-wrapper {
  position: absolute;
  top: 40px;
  bottom: 40px;
  width: 100%;
  background: #DDDDDD;
  border-radius: 2px;
  overflow: hidden;
  box-shadow: 1px 0px 13px 0px rgba(0, 0, 0, 0.75);
}

.loader {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  background: rgba(#ffffff, 0.7);
  display: none;
}

.loader-elem{
  position: absolute;
  top:50%;
  left:50%;
  width: 100px;
  height:100px;
  background: #fe6875;
  margin-left: -50px;
  margin-top: -50px;
  border-radius: 50px;

  animation-name: loader;
  animation-iteration-count: infinite;
  animation-duration: 2s;


  &:after{
    display: block;
    content: '';
    width: 20px;
    height:20px;
    background: #654987;
    position: absolute;
    top:20px;
    left:20px;
    border-radius: 10px;
  }

}

@keyframes loader {
  0%{
    transform: rotate(0deg);
  }
  100%{
    transform: rotate(359deg);
  }
}


.btn {
  display: inline-block;
  text-decoration: none;
  font-size: 41px;
  background: #01AF98;
  border-radius: 3px;
  color: #DDDDDD;
  font-weight: 300;
  text-transform: uppercase;
  padding: 12px 20px;
}

.bottom-wrapper {
  background: #445D73;
  padding: 20px;
  margin-top: 40px;
  position: absolute;
  bottom: 0;
  width: 100%;
  max-height: 50%;
  &:before {
    display: block;
    content: "";
    width: 0;
    height: 0;
    border-style: solid;
    border-width: 0 16px 20px 16px;
    border-color: transparent transparent #445D73 transparent;
    position: absolute;
    left: 50%;
    top: -20px;
    margin-left: -16px;
  }
}

header {
  height: 70px;
  background: #445D73;
}

/**
* Screens
**/

.screen--start {
  text-align: center;
}

.rules {
  margin-top: 30px;
  padding: 20px 100px;
}

// GAme screen
.screen--game {

}

.game-status {
  border-bottom: solid 1px #111111;
  &:after {
    display: block;
    content: '';
    clear: both;
  }
}

.timer {
  float: left;
  &.allert {
    color: #fe1111;
  }
}

.question-count {
  float: right;
}

.timer, .question-count {
  display: inline-block;
  font-size: 40px;
  font-weight: 300;
}

.main-fact {
  font-size: 60px;
  font-weight: bold;
  color: #01AF98;
  text-align: center;
}

.description {
  font-style: italic;
  color: #212121;
  text-align: center;
}

.socondary-fact {
  width: 100%;
  font-size: 77px;
  color: #EDE9E0;
  border: solid 1px rgba(#DDDDDD, 0.2);
  height: 160px;
  display: flex;
  align-content: center;
  text-align: center;
  text-transform: uppercase;
  font-weight: 300;
  margin-bottom: 30px;
}

.btn--before, .btn--after {
  width: 48%;
  text-align: center;
}

.btn--after {
  float: right;
}

.bottom-buttons {
  &:after {
    display: block;
    content: '';
    clear: both;
  }
}

// End game screen

.results {
  margin: 0;
  padding: 0;
  list-style: none;
  background: #EDE9E0;

  li {
    list-style: none;
    border-bottom: solid 1px #BEBAB3;
  }
}

.screen--end, .screen--game {
  display: none;
}




